@import url(https://fonts.googleapis.com/css2?family=Nerko+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
}

.app {
    display: flex;
    flex-direction: column;
    margin: auto;
    max-width: 1400px;
}

#mandarin-image {
    width: 800px;
    align-self: flex-end;
}
.nav {
    list-style-type: none;
    display: flex;
    margin: 30px;
    align-self: flex-end;
    color: #D80000;
    font-family: "Nerko One";
    font-size: 30px;
    
    
}

.nav li {
    margin: 0 10px;
}
.heading {
    align-self: center;
    margin: 60px;
}

.heading h1 {
    font-size: 110px;
    font-family: "Nerko One";
    color:#D80000;
}

.heading p {
    margin-top: -30px;
    text-align: right;
    color:#EE1C1C;
}
.search {
    align-self: center;
}

.search input {
    border: 2px solid red;
    padding: 10px;
    border-radius: 10px;
    width: 300px;
}
